.sp-menu {
    display: none;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.8s;
}

.sp-menu-item span {
    border-bottom: 2rem solid #333333;
    width: 28rem;
    display: block;
    margin-bottom: 6rem;
    transition: all 0.8s;
}

.sp-menu-item span:last-child {
    margin-bottom: unset;
    width: 28rem;
}

.sp-menu-item.change span:first-child {
    -webkit-transform: rotate(-45deg) translate(-2rem, 4rem);
    transform: rotate(-45deg) translate(-2rem, 4rem);
}

.sp-menu-item.change span:nth-child(2) {
    display: none;
}

.sp-menu-item.change span:last-child {
    width: 28rem;
    -webkit-transform: rotate(45deg) translate(-3rem, -4rem);
    transform: rotate(45deg) translate(-3rem, -4rem);
}

.menu-overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: calc(100% - 56px);
    z-index: 20;
    margin-top: 56px;
    padding: 60px 36px;
    background-color: white;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    -webkit-transform: translate(-100%, 0%);
    transform: translate(-100%, 0%);
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    z-index: 99;
    overflow: auto;
}

.menu-overlay.change {
    -webkit-transform: none;
    transform: none;
}

.menu-overlay .overlay-links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .overlay-links__contact {
        width: 100%;
        .l-header__contact {
            margin-top: 20px;
            background: #0355BB;
            background: linear-gradient(90deg, rgba(3, 85, 187, 1) 0%, rgba(0, 154, 229, 1) 100%);
            color: white;
            width: 100%;
            height: 50px;
            border-radius: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

.overlay-links__group {
    margin-bottom: 25px;
    width: 100%;
}

.overlay-links__lead {
    color: #333333;
    font-weight: bold;
    font-size: 16px !important;
    margin-left: unset !important;
    margin-top: 25px;
    text-align: center;
    span {
        &:first-child {
            font-size: 18px;
        }
        &:nth-child(2) {
            font-size: 14px;
        }
    }
}

.overlay-links_contact {
    width: 100%;
    margin-top: 25px;
}

@media(max-width: 1010px) {
    .menu-overlay {
        display: block;
        width: 100%;
    }
    .sp-menu {
        display: flex;
        &-item {
            span {
                border-bottom: 2px solid #333333;
                width: 22px;
                margin-bottom: 5px;
                &:last-child {
                    width: 22px;
                }
            }
            &.change {
                span {
                    &:first-child {
                        transform: rotate(-45deg) translate(-2px, 4px);
                    }
                    &:last-child {
                        width: 22px;
                        transform: rotate(45deg) translate(-1px, -3px);
                    }
                }
            }
        }
    }
    .l-header {
        &__menu {
            display: none;
        }
        &__inner {
            margin-left: 15rem;
            margin-right: 15rem;
        }
    }
}